import { _decorator, Component, Node, tween } from 'cc'
import { Lobby } from './Lobby'
const { ccclass, property } = _decorator

@ccclass('Cat')
export class Cat extends Component {
  @property(Node)
  moveList: Node = null
  @property
  movePostionList = []

  @property
  //  const postionList = this.movePostionList
  moveNode: Node = null
  protected onLoad(): void {
    this.initMovePostionList()
  }

  start() {}

  update(deltaTime: number) {}

  //   this.moveList = []

  moveCat() {
    tween(this.node)
      .to(1, { position: this.moveList.shift() })
      .call(() => {
        if (this.moveList.length <= 0) {
          this.moveList = this.movePostionList
        }
        this.moveCat()
      })
  }

  initMovePostionList() {
    const children = this.moveNode.children
    for (let i = 0; i < children.length; i) {
      this.movePostionList.push(children[i].position)
    }
    this.moveList = this.movePostionList
  }

  ClickCat() {
    Lobby.instance.openDetailsView()
    console.log('-------')
  }
}
